import { defineComponent, computed, ref } from 'vue';
import { useStore } from '@/store';
import { createGroup } from '@/service/groups/creategroup';
import { clearGroup, leaveGroup } from '@/service/groups/deletegroup';
import { getGroups } from '@/service/groups/getgroups';
export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },

  setup() {
    const store = useStore();
    const addGroupDrawer = ref(false);
    const createGroupName = ref('');
    const drawerDir = 'ltr';
    const groups = computed(() => store.state.login.groupsInfo.groups);

    const handleMenuItemClick = group_id => {
      store.dispatch('main/handleGroupIdAction', group_id);
    };

    const handleAddGroupBtn = createGroupName => {
      store.commit('main/changeGroupContent', []);
      store.commit('main/clearNewGroupContent');

      const data = async () => {
        createGroup(createGroupName).then(async () => {
          const groupsInfo = await getGroups();
          console.log(groupsInfo);
          store.commit('login/changeGroupsInfo', groupsInfo);
        }).catch(err => {
          console.log(err);
        });
      };

      data();
      addGroupDrawer.value = !addGroupDrawer.value;
    };

    const handleDeleteGroupBtn = group_id => {
      store.commit('main/changeGroupContent', []);
      store.commit('main/clearNewGroupContent');

      const data = async () => {
        clearGroup(group_id).then(async () => {
          leaveGroup(group_id).then(async () => {
            const groupsInfo = await getGroups();
            store.commit('login/changeGroupsInfo', groupsInfo);
          });
        }).catch(e => console.log(e));
      };

      data();
    };

    return {
      groups,
      handleMenuItemClick,
      addGroupDrawer,
      drawerDir,
      createGroupName,
      handleAddGroupBtn,
      handleDeleteGroupBtn
    };
  }

});